<template>
  <div>
    <batch-import :visible.sync="visible" />
  </div>
</template>

<script>
import { BatchImport } from '@handday/components'
export default {
  name: 'ImportFilePages',
  components: {
    BatchImport
  },
  data () {
    return {
      visible: false
    }
  },
  mounted () {
    setTimeout(() => {
      this.visible = true
    }, 1000)
  }
}
</script>
